import React, { Component, Fragment } from 'react';
import Navigation from '../../components/navigation/Navigation.js';
import Sidebar from '../../components/sidebar/Sidebar.js';
// import Footer from '../../components/footer/Footer.js';
import { Row, Col, Breadcrumb, BreadcrumbItem, Button, Form, FormGroup, Label, Input } from 'reactstrap';
import { Link } from 'react-router-dom';
import { GlobalConsumer } from '../../context/context.js';
import Swal from 'sweetalert2'

class Biodata extends Component {
    update = () => {
        return Swal.fire(
            'Sukses!',
            `Biodata berhasil di update !`,
            'success'
        )
    }
    render() {
        return (
            <Fragment>
                <Navigation />
                <div className="wrapper">
                    <Sidebar />
                    <div className={this.props.state.sidebar ? "wrapper-content-close wrapper-content-blur" : "wrapper-content-open"}>
                        <div className={this.props.state.sidebar ? "container" : "container-fluid"}>
                            <Row className="mt-2 mb-5">
                                <Col md="12">
                                    <Breadcrumb className="breadcrumb-me">
                                        {/* <BreadcrumbItem active>Beranda</BreadcrumbItem> */}
                                        <BreadcrumbItem><Link to="/"><i className="fas fa-home mr-1"></i> Beranda</Link></BreadcrumbItem>
                                        <BreadcrumbItem active>Biodata Dosen</BreadcrumbItem>
                                    </Breadcrumb>
                                    <div className="col-md-12 mx-auto mt-2">
                                        <h4 className="text-center">Biodata Dosen</h4>
                                        <Form className="mt-4">
                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Nomor Induk Dosen Nasional (NIDN)</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="nidn" id="nidn" defaultValue="0024087402" className="" disabled />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Nama Lengkap Dosen (tanpa gelar)</Label>
                                                <Col sm={7}>
                                                    <Input type="text" name="namaDosen" id="namaDosen" defaultValue="Hamidillah Ajie" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Nomor Induk Pegawai (PNS)</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="namaDosen" id="namaDosen" defaultValue="12312313" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Nomor Induk Kependudukan (KTP)</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="namaDosen" id="namaDosen" defaultValue="12313123132" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Nomor Pokok Wajib Pajak (NPWP)</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="namaDosen" id="namaDosen" defaultValue="1231313321321" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Kode Dosen (SIAKAD lama)</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="namaDosen" id="namaDosen" defaultValue="1334" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Tempat Kelahiran</Label>
                                                <Col sm={7}>
                                                    <Input type="text" name="namaDosen" id="namaDosen" defaultValue="Jakarta" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Tanggal Kelahiran</Label>
                                                <Col sm={7}>
                                                    <Input type="date" name="date" id="exampleTime" placeholder="time placeholder" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row inline>
                                                <Label sm={5}>Jenis Kelamin</Label>
                                                <Col sm={7}>
                                                    <Label check className="ml-4">
                                                        <Input type="radio" name="radio2" selected />{' '} Laki-laki
                                                    </Label>
                                                    <Label check className="ml-5">
                                                        <Input type="radio" name="radio2" />{' '} Perempuan
                                                    </Label>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Gelar Depan Dosen (dengan titik setelah gelar)</Label>
                                                <Col sm={7}>
                                                    <Input type="text" name="namaDosen" id="namaDosen" defaultValue="S.Kom" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Tugas Tambahan (Struktural)</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Status Dosen UNJ</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Jabatan Fungsional</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Golongan (PNS)</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Alamat Dosen</Label>
                                                <Col sm={7}>
                                                    <Input type="text" name="namaDosen" id="namaDosen" defaultValue="Jakarta" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Kontak/No.HP Aktif</Label>
                                                <Col sm={7}>
                                                    <Input type="number" name="namaDosen" id="namaDosen" defaultValue="081272817189" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Email Aktif</Label>
                                                <Col sm={7}>
                                                    <Input type="email" name="namaDosen" id="namaDosen" defaultValue="hamidillah@unj.ac.id" className="" />
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Provinsi</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Kabupaten/Kotamadya</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                </Col>
                                            </FormGroup>

                                            <FormGroup row>
                                                <Label for="exampleEmail" sm={5}>Kecamatan</Label>
                                                <Col sm={7}>
                                                    <Input type="select" name="select" id="exampleSelect" className="">
                                                        <option>1</option>
                                                        <option>2</option>
                                                        <option>3</option>
                                                        <option>4</option>
                                                        <option>5</option>
                                                    </Input>
                                                    <Button color="primary" type="button" className="mt-4 btn-sm" onClick={this.update}><i className="fas fa-save"></i> Simpan</Button>
                                                    <Button color="danger" type="reset" className="mt-4 ml-2 btn-sm"><i className="fas fa-undo"></i> Reset</Button>
                                                </Col>
                                            </FormGroup>
                                        </Form>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                        {/* <Footer /> */}
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default GlobalConsumer(Biodata);
